import React, { PureComponent } from "react";
import ReactPlayer from "react-player"
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { isTagShop, isSocialWall } from "../../../../actions/api";
import { reelPlayTimeUpdateData } from "../../../../utils";


const mediaReplaceText = (mediaUrl, networkId) => {
    switch (networkId) {
        case 25:
            const soundCloud = String(mediaUrl).replace("https://w.soundcloud.com/player/?url=", "")
            return soundCloud
            break;

        default:
            return mediaUrl
            break;
    }

}
export default class PopUpVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            videoLoaded: false,
            muted: isSocialWall ? true : false,
            played: 0
        }
        this.VideoWrap = React.createRef();
    }

    setVideoLoaded = event => {
        this.setState({
            videoLoaded: event
        })
    }

    muteToggle = () => {
        this.setState({
            muted: !this.state.muted,
        })
    }
    componentWillUnmount() {
        const { played } = this.state
        if (isTagShop) reelPlayTimeUpdateData(played)
    }
    onUpdateTime = (progress) => {
        try {
            if (isTagShop) {
                const { played } = this.state
                this.setState({
                    played: played + progress.playedSeconds
                })
            }
        }
        catch (e) {
            console.log(e)
        }

    }
    render() {
        const { data, wall } = this.props
        const { videoLoaded, muted } = this.state
        const noMuteIcon = [7, 3, 25]
        const muteIcon = !noMuteIcon.includes(data.network.id) ? true : false
        return <div className={`tb_post_modal_video_holder${videoLoaded ? ' tb_post_video_loaded' : ''}`} ref={this.VideoWrap} key={data.mediaUrl} >
            {muteIcon ?
                <div className="tb_m_mute_btn" onClick={this.muteToggle}>
                    <div className={`tb__icon tb-${!muted ? `unmute` : `mute`}`}><div></div></div>
                </div>
                : ''}
            <ReactPlayer
                className={`tb_post_modal_video video${data.id}`}
                url={mediaReplaceText(data.mediaUrl, data.network.id)}
                data-type="video"
                data-network={data.network.id}
                data-link={data.link}
                data-wall-id={wall.Wall.id}
                data-item-id={data.id}
                data-feed-id={data.feedId ? data.feedId : ""}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': 'auto',
                            muted,
                            poster: data.postFileNew
                            // 'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                        }
                    },
                    file: {
                        forceVideo: true,

                        attributes: {
                            muted,
                            poster: data.postFileNew
                            // onError: (e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)
                        }
                    }
                    // tracks: [
                    //     {kind: 'subtitles', src: `${CloudUrl}/media/subtitles.en.vtt`, srcLang: 'en', default: true},
                    // ]
                }}
                loop={true}
                //onError={(e) => { this.setVideoLoaded(false) }}
                onError={(e) => {
                    if (!data.stories || data.stories == 0) POPUP_VIDEO_RENEW(e, data.id)

                    //this.setVideoLoaded(false)
                }}
                //(data.stories == 0) ? POPUP_VIDEO_RENEW(e) : null}
                autoPlay={true}
                muted={muted}
                volume={1}
                playsinline={true}
                onReady={(e) => { this.setVideoLoaded(true) }}
                playing={true}
                height={'100%'}
                width={'100%'}
                controls={!muteIcon ? true : false}
                style={{ backgroundImage: `url(${data.postFileNew})`, backgroundSize: 'cover', backgroundPosition: 'center', backgroundRepeat: 'no-repeat' }}
                onProgress={this.onUpdateTime}
            />
        </div>
    }
}