import React, { PureComponent } from "react";
import PopUpImage from "./PopUpImage";
import PopUpVideo from "./PopUpVideo";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { isTagEmbed } from '../../../../actions/api'

const mediaDimension = JSON.parse(window.localStorage.getItem('mediaDimension'));
export default class PopUpMediaSlider extends PureComponent {

  constructor(props) {
    super(props);
    this.state = {
      mediaSize: {
        height: 1000,
        width: 1000,
      },
      mediaLoaded: false,
      imgUrl: props.data.postFileNew,
      largerHeight: 0,

      isFirst: false,
      isLast: false,
      currentIndex: 0
    };
    this.splideRef = React.createRef();
  }

  componentWillReceiveProps(nextProps) {
    const { data } = nextProps
    const { ImageUrl } = nextProps;
    if (this.state.imgUrl !== ImageUrl) {
      this.setState({
        imgUrl: ImageUrl,
        mediaLoaded: false,
        mediaSize: {
          height: parseInt(data.mediaHeight),
          width: parseInt(data.mediaWidth)
        }
      })
    }
  }

  componentDidMount() {
    const { data } = this.props
    this.setState({
      mediaSize: {
        height: parseInt(data.mediaHeight),
        width: parseInt(data.mediaWidth)
      }
    })

    this.splideRef.current.splide.on('moved', this.handleSlideChange);
    this.handleSlideChange(); // Check on initial load
  }

  componentWillUnmount() {
    this.splideRef.current.splide.off('moved', this.handleSlideChange);
  }

  handleSlideChange = () => {
    const splide = this.splideRef.current.splide;
    const index = splide.index;
    const isFirst = index === 0;
    const isLast = index === splide.length - 1;

    this.setState({ isFirst, isLast });
  };

  render() {
    const { imageList, data, wall, onUpdateProdImgIdx } = this.props

    const { mediaSize, isFirst, isLast, currentIndex } = this.state
    const settings = {
      //type:'slide',
      rewind: false,
      speed: 400,
      gap: 0,
      autoplay: false,
      autoHeight: true,
      padding: 0,
      pagination: true,
      arrows: true,
      classes: {
        pagination: 'splide__pagination tb_modal_slider_dots',
        page: 'splide__pagination__page tb_modal_slider_dot',
      },
    };
    return <>

      <PopUpImage ImageUrl={data.postFile} data={{ ...data, ...imageList[this.state.currentIndex] }} wall={wall}
        //mediaSizeCallback={this.mediaSize} 
        opacity={0} />
      <div className="tb_post_modal_slide_holder_">
        <Splide hasTrack={false} className="tb_post_modal_media_slider" options={settings} ref={this.splideRef} onActive={(splide, prev, next) => {
          //this.setState({ currentIndex: prev.index }, () => onUpdateProdImgIdx(this.state.currentIndex))
        }}>
          <SplideTrack>
            {imageList.map((imageItem, index) => {
              const autoPlay = currentIndex == index ? true : false
              const isPostFile = isTagEmbed ? imageItem.file : imageItem.postFile
              return <SplideSlide key={index} style={{
                margin: 0,
              }}>
                {(imageItem.type === 2 || imageItem.type === 4 || !autoPlay) ?
                  <PopUpImage key={`multi-slider${index}`} ImageUrl={isPostFile} data={{ link: data.link, network: { id: data.network.id }, id: imageItem.id, filterId: data.filterId, stories: imageItem.stories, feedId: data.feedId, postFileNew: isPostFile, filename: isPostFile, file: isPostFile }} wall={wall}
                    //mediaSizeCallback={this.mediaSize} 
                    opacity={1} />
                  : (imageItem.type === 3 || imageItem.type === 5) ?
                    <PopUpVideo data={{ mediaUrl: isTagEmbed ? imageItem.media : imageItem.mediaFile, link: data.link, network: { id: isTagEmbed ? imageItem.networkId : data.networkId }, id: imageItem.id, filterId: data.filterId, stories: imageItem.stories ? imageItem.stories : 0, postFileNew: isPostFile, filename: isPostFile, file: isPostFile, feedId: data.feedId }} wall={wall}
                      //mediaSizeCallback={this.mediaSize} 
                      opacity={1} /> : null}
              </SplideSlide>
            }
            )}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_post_modal_arrow_wrapper_">
            <div className="splide__arrow splide__arrow--prev tb_post_modal_arrow tb_post_modal_arrow_left__ tb__icon tb-arrow-left-alt" disabled={isFirst} role="button" aria-label="Go to previous slide"><div></div></div>
            <div className="splide__arrow splide__arrow--next  tb_post_modal_arrow tb_post_modal_arrow_right__ tb__icon tb-arrow-right-alt" disabled={isLast} role="button" aria-label="Go to next slide"><div></div></div>
          </div>
        </Splide>
      </div>
    </>
  }
}